import React from "react";

class AthleteInfo extends React.Component {
  state = {
    firstName: "First",
    middleName: "Middle",
    lastName: "Last",
    schoolName: "Test School Name",
    city: "Test City",
    state: "Test State",
    classYear: "Test Junior",
    graduationYear: "Test Grad Year",
    sport: "Baseball",
    sportPosition: "Water Boy",
    relationshipStatus: "It's Complicated",
    height: "7'5''",
    weight: "5 lbs",
    gpa: "5.10"
  };

  render() {
    return (
      <div className="AthleteInfo container">
        <div className="row">
          <div className="col-md-6">
            <h1>
              {this.state.firstName} {this.state.middleName} {this.state.lastName}
            </h1>
          </div>
        </div>
        <hr />
        <div className="row">
          <div className="col-md-7">
            <h2>School: {this.state.schoolName}</h2>
          </div>
          <div className="col-md-5">
            <h2>Class: {this.state.classYear}</h2>
          </div>
        </div>

        <div className="row">
          <div className="col-md-7">
            <h2>
              {this.state.city}, {this.state.state}
            </h2>
          </div>
          <div className="col-md-5">
            <h2>Graduation Year: {this.state.graduationYear}</h2>
          </div>
        </div>
        <hr />
        <div className="col-md-12">
          <div className="row">
            <h1>
              {this.state.sport} | {this.state.sportPosition} | {this.state.relationshipStatus}
            </h1>
          </div>
          <hr />
          <div className="row">
            <h1>
              Height: {this.state.height} | Weight: {this.state.weight} | GPA: {this.state.gpa}
            </h1>
          </div>{" "}
        </div>
      </div>
    );
  }
}
export default AthleteInfo;



