import React from "react";
import AthleteInfo from "../../app/routes/Athlete/AthleteInfo"; /* 
import AthleteComponent2 from "../../app/routes/Athlete/AthleteComponent2"; */

class ProfileInfo extends React.Component {
  render() {
    return (
      <div className="col-lg-12">
        <div className="jr-card">
          <div className="app-main-content">
            <div className="row">
              <div className="col-lg-3">
                <img
                  src="https://bringmethenews.com/.image/t_share/MTU0MDQ3MjMzOTE1ODg4NzIy/image-placeholder-title.jpg"
                  width="300px"
                  height="300px"
                  className="img-responsive"
                  /* style="padding-top: 5px" */
                />
              </div>
              <div className="col-lg-9">
                <div className="row">
                  <div className="col-lg-7">
                    <AthleteInfo />
                    <div className="row" />
                  </div>
                  <div className="col-lg-5">
                    {" "}
                    <AthleteComponent2 />{" "}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default ProfileInfo;



